import React, { Component } from "react";

class FacebookComment extends Component {
  render() {
    const { href } = this.props;
    return (
      <div>
        <div
          className="fb-comments"
          data-href={href}
          data-width="100%"
          data-numposts="5"
        />
        <div id="fb-root" />
      </div>
    );
  }
}

export default FacebookComment;
